import { isPendingPage } from 'root/isPage';
import { nameLabelArrayTypeOfHelp } from 'service/typeOfHelp/dataFromTypeOfHelp';
import { DataHistory } from './DataHistory';
import {
  Container,
  Block,
  Label,
  Info,
  CommentContainer,
} from './MoreInfo.styled';

export const MoreInfo = ({ additional, info, dates, type }) => {
  return (
    <Block>
      <hr />
      <div>
        {nameLabelArrayTypeOfHelp().map(({ type, label }) => {
          return (
            additional[type] && (
              <Container key={type}>
                <Label> {label} : </Label>
                <Info>{additional[type]}</Info>
              </Container>
            )
          );
        })}
      </div>

      {info && (
        <>
          <hr />
          <CommentContainer>
            <Label> —ñ–Ω—Ñ–æ—Ä–º–∞—Ü—ñ—è : </Label>
            <Info>{info}</Info>
          </CommentContainer>
        </>
      )}
      {!isPendingPage(type) && (
        <>
          <hr />
          <DataHistory dates={dates} type={type} />
        </>
      )}
    </Block>
  );
};
